.list{
    background: #fff;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.item{
    display: block;
    border: 1px solid #eee;padding: 5px;
}
.title{
    color: black;

}
.img{
    width: 100px;
    height: 100px;
    object-fit: cover;
    object-position: center;
}
.itemLink{
    list-style-type:none;
}
.itemHref{
    color: green;
    text-decoration: underline red;


}
.itemDescription{
    color: black;
}