:root{
  *{
    padding:0;
    margin:0;
    box-sizing: border-box;
  }
}
.App{
  max-width: 800px;
  min-height: 400px;
  height: auto;
  background: #eee;
  text-align: center;
  margin: 10px auto;
  padding: 20px;
  border: 1px solid red;
  &>*{
    border: 1px solid red;
    border-radius: 5px;
    padding:5px;
  }
  display: grid;
  grid-template-areas: 
    "head head"
    "main side"
    "main side"
    "footer footer";
    gap: 5px;
  .head{
    background: rgb(29, 29, 29);
    grid-area: head;
  }
  .main{
    grid-area: main;
  }
  .side{
    grid-area: side;
  }
  .footer{
    grid-area: footer;
  }

}

